<template>
  <div>
    <el-divider content-position="left"></el-divider>
    <el-row class="flex-wrap" :gutter="20">
      <el-col :xl="12" class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="70"></el-table-column>
            <el-table-column prop="name" label="" width="180"></el-table-column>
            <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :xl="12" class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-table :data="tableData" stripe style="width: 100%">
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50"></el-table-column>
            <el-table-column prop="name" label="" width="120"></el-table-column>
            <el-table-column prop="sex" label="" width="60"></el-table-column>
            <el-table-column prop="age" label="" width="60"></el-table-column>
            <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :xl="12" class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-table :data="tableData" style="width: 100%" :row-class-name="tableRowClassName">
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50"></el-table-column>
            <el-table-column prop="name" label="" width="120"></el-table-column>
            <el-table-column prop="sex" label="" width="60"></el-table-column>
            <el-table-column prop="age" label="" width="60"></el-table-column>
            <el-table-column prop="address" label="" width="200" show-overflow-tooltip></el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :xl="12" class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop="id" label="ID" width="50"></el-table-column>
            <el-table-column prop="name" label="" width="120"></el-table-column>
            <el-table-column prop="sex" label="" width="60"></el-table-column>
            <el-table-column prop="age" label="" width="60"></el-table-column>
            <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
            <el-table-column fixed="right" label="" width="100">
              <template slot-scope="scope">
                <el-button
                  @click="handleClick(scope.row)"
                  type="text"
                  size="small"
                  class="padding-0"
                ></el-button>
                <el-button type="text" size="small" class="padding-0"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>

    <el-divider content-position="left"></el-divider>

    <el-row :gutter="20">
      <el-col class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
            <div style="float:right;display:inlne-block;display: flex;align-items: center;">
              <el-radio-group v-model="styleValue" @change="styleChange">
                <el-radio label="false"></el-radio>
                <el-radio label="true"></el-radio>
              </el-radio-group>
              <el-checkbox-group
                v-model="pageOptions"
                @change="pageOptionsChange"
                style="margin-left: 35px;"
              >
                <el-checkbox label="total"></el-checkbox>
                <el-checkbox label="sizes"></el-checkbox>
                <el-checkbox label="prev"></el-checkbox>
                <el-checkbox label="pager"></el-checkbox>
                <el-checkbox label="next"></el-checkbox>
                <el-checkbox label="jumper"></el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
          <el-table :data="tableData" stripe style="width: 100%;margin-bottom:20px;">
            <el-table-column prop="id" label="ID" width="70"></el-table-column>
            <el-table-column prop="name" label="" width="180"></el-table-column>
            <el-table-column prop="sex" label="" width="180"></el-table-column>
            <el-table-column prop="age" label="" width="180"></el-table-column>
            <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
          </el-table>
          <el-pagination
            :background="pageBackground"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="1"
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="10"
            :layout="optionsArr.join(',')"
            :total="100"
          ></el-pagination>
        </el-card>
      </el-col>
      <el-col class="mb20">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <vxe-table border :data="tableData2" :tree-config="{children: 'children'}">
            <vxe-table-column type="index" width="100" title=""></vxe-table-column>
            <vxe-table-column field="name" title="" width="150" tree-node></vxe-table-column>
            <vxe-table-column field="size" title="" width="150"></vxe-table-column>
            <vxe-table-column field="date" title=""></vxe-table-column>
          </vxe-table>
        </el-card>
      </el-col>
    </el-row>

    <el-divider content-position="left"></el-divider>

    <el-row :gutter="30">
      <el-col :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
            <el-radio-group v-model="dropType" style="float:right" @change="dropTypeChange">
              <el-radio label="row"></el-radio>
              <el-radio label="col"></el-radio>
            </el-radio-group>
          </div>
          <!--  -->
          <vxe-table v-show="dropType == 'row'" border row-key ref="xTable1" :data="tableData">
            <vxe-table-column width="60" fixed>
              <template>
                <span class="drag-btn">
                  <i class="vxe-icon--menu"></i>
                </span>
              </template>
              <template v-slot:header>
                <vxe-tooltip v-model="showHelpTip1" content="">
                  <i class="vxe-icon--question" @click="showHelpTip1 = !showHelpTip1"></i>
                </vxe-tooltip>
              </template>
            </vxe-table-column>
            <vxe-table-column field="name" title="" width="120"></vxe-table-column>
            <vxe-table-column field="sex" title="" width="100"></vxe-table-column>
            <vxe-table-column field="age" title="" width="100"></vxe-table-column>
            <vxe-table-column field="address" title="" show-overflow></vxe-table-column>
          </vxe-table>
          <!--  -->
          <vxe-table v-if="dropType == 'col'" border column-key ref="xTable2" :data="tableData">
            <vxe-table-column field="id" title="ID" width="60" fixed></vxe-table-column>
            <vxe-table-column field="name" title="" width="120"></vxe-table-column>
            <vxe-table-column field="sex" title="" width="100"></vxe-table-column>
            <vxe-table-column field="age" title="" width="100"></vxe-table-column>
            <vxe-table-column field="address" title="" show-overflow></vxe-table-column>
          </vxe-table>
        </el-card>
      </el-col>
      <el-col :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span>
            <el-radio-group v-model="editType" style="float:right">
              <el-radio label="row"></el-radio>
              <el-radio label="cell"></el-radio>
            </el-radio-group>
          </div>
          <vxe-table
            border
            resizable
            show-overflow
            :data="tableData"
            :edit-config="{trigger: 'click', mode: editType}"
            @edit-actived="editActivedEvent"
            @edit-closed="editClosedEvent"
            :key="editTypeChange"
          >
            <vxe-table-column field="id" title="ID" width="60"></vxe-table-column>
            <vxe-table-column field="name" title="" :edit-render="{name: 'input'}"></vxe-table-column>
            <vxe-table-column
              field="sex"
              title=""
              width="100"
              :edit-render="{name: 'select', options: [{label:'',value:''},{label:'',value:''}],optionProps: {value: 'value', label: 'label'}}"
            ></vxe-table-column>
            <vxe-table-column field="age" title="" width="100" :edit-render="{name: 'input'}"></vxe-table-column>
            <vxe-table-column field="address" title="" :edit-render="{name: 'input'}"></vxe-table-column>
          </vxe-table>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Sortable from "sortablejs";
export default {
  data() {
    return {
      pageBackground: false,
      styleValue: "false",
      optionsArr: ["total", "prev", "pager", "next"],
      pageOptions: ["total", "prev", "pager", "next"],
      showHelpTip1: false,
      dropType: "row",
      tableData: [
        {
          id: "1",
          name: "",
          sex: "",
          age: 18,
          address: " 1518 "
        },
        {
          id: "2",
          name: "",
          sex: "",
          age: 22,
          address: " 1518  7  302"
        },
        {
          id: "3",
          name: "",
          sex: "",
          age: 13,
          address: " 1518 "
        },
        {
          id: "4",
          name: "",
          sex: "",
          age: 20,
          address: " 1518 "
        }
      ],
      editType: "row",
      tableData2: [
        {
          name: "",
          size: "1G",
          date: "2019-10-11 12:20:14",
          children: [
            {
              name: "",
              size: "1G",
              date: "2019-10-11 12:20:14",
              children: []
            },
            {
              name: "",
              size: "1G",
              date: "2019-10-11 12:20:14"
            }
          ]
        },
        {
          name: "",
          size: "1G",
          date: "2019-10-11 12:20:14",
          children: []
        },
        {
          name: "",
          size: "1G",
          date: "2019-10-11 12:20:14",
          children: []
        },
        {
          name: "",
          size: "1G",
          date: "2019-10-11 12:20:14",
          children: []
        }
      ]
    };
  },
  created() {
    this.rowDrop();
  },
  computed: {
    editTypeChange() {
      return this.editType;
    }
  },
  watch: {},
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return "warning-row";
      } else if (rowIndex === 3) {
        return "success-row";
      }
      return "";
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    styleChange: function(value) {
      this.pageBackground = value == "true";
    },
    pageOptionsChange: function(value) {
      let arr = ['total','sizes','prev','pager','next','jumper']
      let rtnArr = []
      let temp = []
      value.forEach(item => {
        var index = arr.indexOf(item)
        temp[index] = item
      });
      temp.forEach(item => {
        rtnArr.push(item)
      });
      this.optionsArr = rtnArr;
      console.log(rtnArr);
    },
    dropTypeChange(val) {
      if (val == "col") {
        this.columnDrop();
      } else {
        this.rowDrop();
      }
    },
    rowDrop() {
      this.$nextTick(() => {
        let xTable = this.$refs.xTable1;
        Sortable.create(
          xTable.$el.querySelector(".body--wrapper>.vxe-table--body tbody"),
          {
            handle: ".drag-btn",
            onEnd: ({ newIndex, oldIndex }) => {
              let currRow = this.tableData.splice(oldIndex, 1)[0];
              this.tableData.splice(newIndex, 0, currRow);
            }
          }
        );
      });
    },
    columnDrop() {
      this.$nextTick(() => {
        let xTable = this.$refs.xTable2;
        Sortable.create(
          xTable.$el.querySelector(
            ".body--wrapper>.vxe-table--header .vxe-header--row"
          ),
          {
            handle: ".vxe-header--column:not(.col--fixed)",
            onEnd: ({ item, newIndex, oldIndex }) => {
              let { fullColumn, tableColumn } = xTable.getTableColumn();
              let targetThElem = item;
              let wrapperElem = targetThElem.parentNode;
              let newColumn = fullColumn[newIndex];
              if (newColumn.fixed) {
                // 
                if (newIndex > oldIndex) {
                  wrapperElem.insertBefore(
                    targetThElem,
                    wrapperElem.children[oldIndex]
                  );
                } else {
                  wrapperElem.insertBefore(
                    wrapperElem.children[oldIndex],
                    targetThElem
                  );
                }
                return this.$XModal.message({
                  message: "",
                  status: "error"
                });
              }
              // 
              let oldColumnIndex = xTable.getColumnIndex(tableColumn[oldIndex]);
              let newColumnIndex = xTable.getColumnIndex(tableColumn[newIndex]);
              // 
              let currRow = fullColumn.splice(oldColumnIndex, 1)[0];
              fullColumn.splice(newColumnIndex, 0, currRow);
              xTable.loadColumn(fullColumn);
            }
          }
        );
      });
    },
    editActivedEvent({ row, column }, event) {
      console.log(` ${column.title} `);
    },
    editClosedEvent({ row, column }, event) {
      console.log(` ${column.title} `);
    }
  }
};
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
.drag-btn {
  cursor: move;
  font-size: 12px;
}
</style>


